import {
  useDisclosure,
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  DrawerHeader,
  DrawerBody,
  Input,
  DrawerFooter,
  Button,
  SimpleGrid,
  Box,
  Image,
} from '@chakra-ui/core';

export default function DrawerCalendar(props) {
  const { isOpen, onClose } = props;
  const btnRef = React.useRef();
  const imageUrl =
    'https://img-a.udemycdn.com/course/240x135/2056209_90a3_2.jpg';
  return (
    <>
      <Drawer
        isOpen={isOpen}
        placement='right'
        onClose={onClose}
        finalFocusRef={btnRef}
        size={'md'}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Curso Presencial!</DrawerHeader>
          <SimpleGrid pr='20px' pl='20px' columns={1} spacing={2}>
            <Box>
              <Image w='100% ' src={imageUrl} />
            </Box>
          </SimpleGrid>
          <DrawerBody>
            <Input placeholder='Type here...' />
          </DrawerBody>

          <DrawerFooter>
            <Button variant='outline' mr={3} onClick={onClose}>
              Cancel
            </Button>
            <Button color='blue'>Save</Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </>
  );
}
